<template>
  <div class="sellerList">
    <div class="table_title">
      <div>
        <el-button
          v-if="isShow"
          type="primary"
          size="mini"
          class="el-icon-d-arrow-left"
          @click="back()"
        ></el-button>
      </div>
      <!-- <el-button
        type="primary"
        size="small"
        icon="el-icon-plus"
        @click="openAddEditDialog('add')"
      ></el-button>-->
      <div class="search_wrap">
        <el-input
          clearable
          v-model="checker"
          placeholder=""
          size="mini"
          style="width:200px;margin-right:10px;"
          @keyup.enter.native="search"
        ></el-input>
        <el-input
          clearable
          v-model="name"
          placeholder=""
          size="mini"
          style="width:200px"
          @keyup.enter.native="search"
        ></el-input>
        <el-select
          clearable
          v-model="status"
          placeholder=""
          size="mini"
          style="margin:0 10px"
          @keyup.enter.native="search"
        >
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="-1"></el-option>
        </el-select>
        <el-date-picker
          size="mini"
          v-model="date"
          type="daterange"
          align="right"
          unlink-panels
          value-format="timestamp"
          range-separator=""
          start-placeholder=""
          end-placeholder=""
          :picker-options="pickerOptions"
        ></el-date-picker>
        <el-button
          @click="search"
          type="primary"
          icon="el-icon-search"
          size="mini"
          style="margin-left:10px"
        ></el-button>
      </div>
    </div>
    <div class="content">
      <el-table
        :data="dataList.list"
        stripe
        border
        v-loading="loading"
        @cell-click="toGoodlist"
      >
        <el-table-column type="expand">
          <template slot-scope="props">
            <div class="expand_wrap">
              <p>
                <span>:</span>
                {{props.row.idcard}}
              </p>
              <p>
                <span>:</span>
                <img class="idcard_img" :src="props.row.picOn" />
                <img class="idcard_img" :src="props.row.picOff" />
              </p>
              <p>
                <span>:</span>
                <img class="license_img" :src="props.row.license" />
              </p>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="city" label=""></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="shopname" label=""></el-table-column>
        <el-table-column prop="address" label=""></el-table-column>
        <el-table-column prop="tel" label=""></el-table-column>
        <el-table-column prop="guide_num" label=""></el-table-column>
        <el-table-column prop="checker" label=""></el-table-column>
        <el-table-column prop="times" label=""></el-table-column>
        <el-table-column prop label="">
          <template slot-scope="scope">
            <!-- :title="scope.row.status=='1'?'':''"
            @click="userStop(scope.row.id)"-->
            <el-button
              class="mini-button"
              size="mini"
              :type="scope.row.status=='1'?'success':'info'"
            >{{scope.row.status | userStatus}}</el-button>
          </template>
        </el-table-column>

        <el-table-column prop label="" width="300px">
          <template slot-scope="scope">
            <el-button
              class="mini-button"
              @click="openPunishDialog(scope.row)"
              type="warning"
              size="mini"
              icon="el-icon-edit-outline"
            ></el-button>
            <el-button
              class="mini-button"
              @click="openAddEditDialog('edit',scope.row)"
              type="primary"
              size="mini"
              icon="el-icon-edit"
            ></el-button>
            <el-button
              @click="cancel(scope.row.id)"
              class="mini-button"
              type="danger"
              size="mini"
              icon="iconfont chexiao1"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="limit"
        layout="total, sizes, prev, pager, next, jumper"
        :total="dataList.total"
      ></el-pagination>
    </div>

    <!-- dialog -->
    <el-dialog
      top="20px"
      :title="form.id?'':''"
      :visible.sync="AddEditDialog"
      width="650px"
      @close="$refs['ruleForm'].resetFields()"
    >
      <el-form status-icon :model="form" :rules="rules" ref="ruleForm" label-width="120px">
        <el-form-item label="">
          <el-cascader
            :options="cityData"
            @change="currentCity"
            v-model="selectCity"
            change-on-select
            :placeholder="form.id?'':''"
          ></el-cascader>
          <span v-if="form.id" style="margin-left:20px">:{{city}}</span>
        </el-form-item>
        <el-form-item v-if="!form.id" label="" :prop="form.id?'':'username'">
          <el-input type="username" v-model="form.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :prop="form.id?'':'password'">
          <el-input
            type="password"
            v-model="form.password"
            :placeholder="form.id?'':''"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="tel">
          <el-input v-model="form.tel" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="shopname">
          <el-input v-model="form.shopname" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="shop_type">
          <el-select v-model="form.shop_type" placeholder="">
            <el-option
              v-for="item in types"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <template>
            <el-radio v-model="form.commission" label="1" @change="explanation"></el-radio>
            <el-radio v-model="form.commission" label="2" @change="explanation"></el-radio>
          </template>
          <span class="iconfont BAI-heirenwenhao" @click="explanation()" style="cursor:pointer"></span>
        </el-form-item>
        <el-form-item label="" prop="discount" v-if="form.commission=='1'">
          <el-input v-model="form.discount" placeholder="">
            <template slot="append">%</template>
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.shop_info" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="form.address" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="open_times">
          <el-input v-model="form.open_times" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="shop_avatar">
          <el-upload
            :action="`${axios.defaults.baseURL}/common/upload/file/upload_dir`"
            accept="image/jpeg, image/gif, image/png, image/bmp"
            :before-upload="beforeUp5"
            :show-file-list="false"
            :on-success="upSuc5"
          >
            <el-button size="small" type="primary"></el-button>
          </el-upload>
          <img v-if="form.shop_avatar" class="license_img" :src="form.shop_avatar" />
        </el-form-item>
        <el-form-item label="" prop="idcard">
          <el-input v-model="form.idcard" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="picOn">
          <el-upload
            :action="`${axios.defaults.baseURL}/common/upload/file/sfz_upload_dir`"
            accept="image/jpeg, image/gif, image/png, image/bmp"
            :before-upload="beforeUp1"
            :show-file-list="false"
            :on-success="upSuc1"
          >
            <el-button size="small" type="primary"></el-button>
          </el-upload>
          <img v-if="form.picOn" class="idcard_img" :src="form.picOn" />
        </el-form-item>
        <el-form-item label="" prop="picOff">
          <el-upload
            :action="`${axios.defaults.baseURL}/common/upload/file/sfz_upload_dir`"
            accept="image/jpeg, image/gif, image/png, image/bmp"
            :before-upload="beforeUp2"
            :show-file-list="false"
            :on-success="upSuc2"
          >
            <el-button size="small" type="primary"></el-button>
          </el-upload>
          <img v-if="form.picOff" class="idcard_img" :src="form.picOff" />
        </el-form-item>
        <el-form-item label="" prop="license">
          <el-upload
            :action="`${axios.defaults.baseURL}/common/upload/file/license_upload_dir`"
            accept="image/jpeg, image/gif, image/png, image/bmp"
            :before-upload="beforeUp3"
            :show-file-list="false"
            :on-success="upSuc3"
          >
            <el-button size="small" type="primary"></el-button>
          </el-upload>
          <img v-if="form.license" class="license_img" :src="form.license" />
        </el-form-item>
        <el-form-item label="" prop="other">
          <el-upload
            :action="`${axios.defaults.baseURL}/common/upload/file/upload_dir`"
            accept="image/jpeg, image/gif, image/png, image/bmp"
            :before-upload="beforeUp4"
            :show-file-list="false"
            :on-success="upSuc4"
          >
            <el-button size="small" type="primary"></el-button>
          </el-upload>
          <img v-if="form.other" class="license_img" :src="form.other" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="AddEditDialog = false"> </el-button>
        <el-button type="primary" @click="addEdit"> </el-button>
      </div>
    </el-dialog>

    <!-- dialog -->
    <el-dialog title="" :visible.sync="punishDialog" label-width="120px" width="30%">
      <el-form :model="form">
        <el-form-item label="">
          <el-select
            v-model="punishType"
            clearable
            placeholder=""
            @change="changePunishType"
          >
            <el-option
              v-for="item in punishList"
              :key="item.value"
              :label="item.title"
              :value="item.val"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" v-if="punishType">
          <el-select v-model="punishContent" clearable placeholder="">
            <el-option
              v-for="item in punishContentList"
              :key="item.value"
              :label="item.label"
              :value="item.ps"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="punishDialog = false"> </el-button>
        <el-button type="primary" @click="punishSure"> </el-button>
      </span>
    </el-dialog>
    <!-- dialog -->
    <el-dialog title="" :visible.sync="previewDialog" width="800px" top="20px">
      <div v-html="form.shop_info"></div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="previewDialog = false"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import citys from "../../utils/city.js";
import { constants } from "crypto";
export default {
  data() {
    return {
      loading: false,
      isShow: false,
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" }
        ],
        tel: [
          { validator: this.$rules.checkPhone, required: true, trigger: "blur" }
        ],
        name: [{ required: true, message: "", trigger: "blur" }],
        address: [
          { required: true, message: "", trigger: "blur" }
        ],
        idcard: [
          { validator: this.$rules.checkId, required: true, trigger: "blur" }
        ],
        picOn: [
          { required: true, message: "", trigger: "blur" }
        ],
        picOff: [
          { required: true, message: "", trigger: "blur" }
        ],
        shopname: [
          { required: true, message: "", trigger: "blur" }
        ],
        open_times: [
          { required: true, message: "", trigger: "blur" }
        ],
        license: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      pickerOptions: {
        //
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      username: "", //
      checker:"",//
      name: "",
      status: "", //
      date: "", //
      dataList: [], //
      page: 1, //
      limit: 10, //
      AddEditDialog: false,
      previewDialog: false, //
      cityData: citys, //
      selectCity: [], //
      city: "",
      form: {
        username: "",
        password: "",
        name: "",
        shop_info: "", //
        shop_type: "", //
        commission: "", //  1  2
        discount: "", //
        address: "",
        tel: "",
        idcard: "",
        picOn: "",
        picOff: "",
        license: "",
        other: "",
        shop_avatar: "",
        shopname: "",
        open_times: ""
      },
      autoChecke: {
        //
        name: "",
        idcard: "",
        picOn: "",
        picOff: "",
        license: ""
      },
      punishDialog: false, //dialog
      punishList: "",
      punishContentList: "",
      punishId: "", //id
      punishType: "",
      punishContent: "",
      types: [
        { label: "", value: 1 },
        { label: "", value: 2 }
      ] //
    };
  },
  watch: {
    punishType(newVal, oldVal) {
      console.log(newVal);
      this.punishList.forEach(item => {
        if (newVal == item.val) {
          this.punishContentList = item.type;
        }
      });
    }
  },
  components: {},
  methods: {
    explanation() {
      this.$message({
        showClose: true,
        dangerouslyUseHTMLString: true,
        message:
          "<strong><p>&nbsp;<p></strong>",
        duration: 5000
      });
    },
    // limit
    handleSizeChange(val) {
      this.limit = val;
      this.getDataList();
    },
    //  page
    handleCurrentChange(val) {
      this.page = val;
      this.getDataList();
    },
    //
    getDataList() {
      this.loading = true;
      this.$api
        .getSellerList({
          page: this.page,
          limit: this.limit,
          status: this.status,
          times: this.date,
          name: this.name,
          checker:this.checker,
        })
        .then(res => {
          console.log(res);
          this.dataList = res.data || [];
          if (res.code) {
            this.$message[res.code ? "warning" : "success"](res.data);
          }
          this.loading = false;
        });
    },
    currentCity(e) {
      console.log(e);
    },

    //
    search() {
      this.page = 1;
      this.getDataList();
      this.isShow = true;
    },

    back() {
      this.page = 1;
      this.limit = 10;
      this.status = "";
      this.date = "";
      this.name = "";
      this.checker="";
      this.getDataList();
      this.isShow = false;
    },
    // 
    userStop(id) {
      this.$confirm("?", "", { type: "warning" })
        .then(() => {
          this.$api
            .userStop({
              uid: id,
              result: ""
            })
            .then(res => {
              this.$message[res.code ? "warning" : "success"](res.data);
              this.getDataList();
            });
        })
        .catch(() => {
          this.$message.info("");
        });
    },
    //dialog
    openAddEditDialog(type, item) {
      if (type == "add") {
        for (let i in this.form) {
          this.form[i] = "";
        }
        this.selectCity = [];
      } else {
        this.form.username = item.username;
        this.form.password = item.password || "";
        this.city = item.city;
        this.selectCity = [];
        this.form.name = item.name;
        this.form.tel = item.tel;
        this.form.address = item.address;
        this.form.idcard = item.idcard;
        this.form.id = item.id;
        this.form.picOn = item.picOn;
        this.form.picOff = item.picOff;
        this.form.license = item.license;
        this.form.shop_info = item.shop_info;
        this.form.shop_type = item.shop_type;
        this.form.commission = item.commission;
        this.form.discount = item.discount;
        this.form.shopname = item.shopname;
        this.form.shop_avatar = item.shop_avatar;
        this.form.other = item.other;
        this.form.open_times = item.open_times || "";
      }
      this.AddEditDialog = true;
    },
    //
    beforeUp1(file) {
      if (file.size > 1024 * 2 * 1024) {
        // 2m  
        this.$message.warning("2MB");
        return false;
      }
    },
    //
    upSuc1(res, file, fileList) {
      console.log(res);
      if (res.code) {
        return;
      } else {
        this.form.picOn = res.data.host + res.data.name;
      }
    },
    //
    beforeUp2(file) {},
    //
    upSuc2(res, file, fileList) {
      console.log(res);
      if (res.code) {
        return;
      } else {
        this.form.picOff = res.data.host + res.data.name;
      }
    },
    //
    beforeUp3(file) {},
    //
    upSuc3(res, file, fileList) {
      console.log(res);
      if (res.code) {
        return;
      } else {
        this.form.license = res.data.host + res.data.name;
      }
    },
    //
    beforeUp4(file) {},
    //
    upSuc4(res, file, fileList) {
      console.log(res);
      if (res.code) {
        return;
      } else {
        this.form.other = res.data.host + res.data.name;
      }
    },
    beforeUp5(file) {},
    //
    upSuc5(res, file, fileList) {
      console.log(res);
      if (res.code) {
        return;
      } else {
        this.form.shop_avatar = res.data.host + res.data.name;
      }
    },
    ///
    addEdit() {
      let that = this;
      if (!this.form.id) {
        if (!that.selectCity || !that.selectCity.length)
          return this.$message.warning("");
      }
      this.autoChecke.name = this.form.name;
      this.autoChecke.idcard = this.form.idcard;
      this.autoChecke.picOn = this.form.picOn;
      this.autoChecke.picOff = this.form.picOff;
      this.autoChecke.license = this.form.license;
      that.$refs.ruleForm.validate(valid => {
        if (valid) {
          that.$api[that.form.id ? "editSeller" : "addSeller"]({
            username: that.form.username,
            password: that.form.password,
            tel: that.form.tel,
            citycode: that.selectCity,
            id: that.form.id || "",
            shop_type: that.form.shop_type,
            discount: that.form.discount,
            commission: that.form.commission,
            shopname: that.form.shopname,
            shop_info: that.form.shop_info,
            address: that.form.address,
            shop_avatar: that.form.shop_avatar || "",
            autoChecke: that.autoChecke,
            name: that.form.name,
            open_times: that.form.open_times || "",
            material: {
              name: that.form.name,
              address: that.form.address,
              idcard: that.form.idcard,
              picOn: that.form.picOn,
              picOff: that.form.picOff,
              license: that.form.license,
              other: that.form.other
            }
          }).then(res => {
            that.$message[res.code ? "error" : "success"](res.data.message);
            if (!that.form.id) {
              that.page = 1;
            }
            that.getDataList();
            that.AddEditDialog = res.code ? true : false;
          });
        } else {
          return false;
        }
      });
    },

    //
    cancel(id) {
      this.$confirm("", "", {
        type: "warning"
      }).then(() => {
        this.loading = true;
        this.$api.cancelSeller({ id: id }).then(res => {
          this.$message[res.code ? "warning" : "success"](res.data.message);
          this.loading = false;
          this.getDataList();
        });
      });
    },

    //
    del(id) {
      this.$confirm("?", "", { type: "warning" })
        .then(() => {
          this.$api
            .delSeller({
              id: id
            })
            .then(res => {
              this.$message[res.code ? "error" : "success"](res.data.message);
              this.page = this.$options.filters.pagination(
                this.page,
                this.limit,
                this.dataList.total
              );
              this.getDataList();
            });
        })
        .catch(() => {
          this.$message.info("");
        });
    },

    //
    getPunishType() {
      this.$api.getPunishType().then(res => {
        this.punishList = res.data;
      });
    },
    //dialoig
    openPunishDialog(item) {
      this.getPunishType();
      this.punishId = item.id;
      this.punishDialog = true;
    },

    //
    changePunishType() {
      this.punishContent = "";
    },

    //
    punishSure() {
      if (!this.punishType || !this.punishContent)
        return this.$message.warning("");
      this.$api
        .addPunish({
          to_uid: this.punishId,
          types: this.punishType,
          values: this.punishContent
        })
        .then(res => {
          this.$message[res.code ? "warning" : "success"](res.data.message);
          this.punishDialog = res.code ? true : false;
        });
    },
    toGoodlist(row, column, cell, event) {
      if (column.label == "") {
        this.$router.push({ path: "/goodList", query: { id: row.id } });
      }
      if (column.label == "") {
        this.$router.push({ path: "/guidesList", query: { id: row.id } });
      }
    }
  },
  created() {
    this.getDataList();
  }
};
</script>

<style scoped>
.content {
  background-color: #fff;
  padding: 20px;
  box-sizing: border-box;
  width: 100%;
  overflow: auto;
}
.license {
  width: 200px;
  height: 300px;
}
.idcard_box {
  padding: 20px;
  box-sizing: border-box;
}
.el-button [class*="el-icon-"] + span {
  margin-left: 5px;
}
</style>
